import Navbar from './components/layout/Navbar';
import Landing from './components/layout/Landing';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Fragment, useEffect } from 'react';
import Login from './components/auth/Login';
import Register from './components/auth/Register';
import './App.css';
import Alert from './components/layout/Alert';
import ProfileForm from './components/profile-forms/ProfileForm';
import Scrollbars from 'react-custom-scrollbars';
import bottomTransitionWave from './img/bottomTransitionWave.svg';

//Redux
import { Provider } from 'react-redux'; //Provider connects react with redux

import store from './store';

import setAuthToken from './utils/setAuthToken';
import { loadUser } from './actions/auth';
import Dashboard from './components/dashboard/Dashboard';
import PrivateRoute from './components/routing/PrivateRoute';

import Profiles from './components/profiles/Profiles';
import AOS from 'aos';
import 'aos/dist/aos.css';

const App = () => {
  if (localStorage.token) {
    setAuthToken(localStorage.token);
  }
  useEffect(() => {
    store.dispatch(loadUser());
    AOS.init({
      duration: 1000,
      once: true,
    });
  }, []);

  const renderThumbVertical = ({ style, ...props }) => (
    <div
      {...props}
      style={{
        ...style,
        width: '20px',
        backgroundColor: '#00000070',
        zIndex: 1501,
      }}
    />
  );

  // HERE, [] RUN THE CODE OF useEffect only once. if we change [] with int i =1,
  // then everytime i changes, the snippet code with run

  return (
    <Provider store={store}>
      <Router>
        <div class='bg'></div>

        <Scrollbars style={{ height: '100vh', width: '100vw' }} renderThumbVertical={renderThumbVertical}>
          <Alert />
          <Navbar />
          <Route exact path='/' component={Landing} />

          <section className='container landing'>
            <Switch>
              <PrivateRoute exact path='/dashboard' component={Dashboard} />
              <Route exact path='/login' component={Login} />
              <Route exact path='/register' component={Register} />
              <PrivateRoute exact path='/profiles' component={Profiles} />
              <PrivateRoute exact path='/create-profile' component={ProfileForm} />
              <PrivateRoute exact path='/edit-profile' component={ProfileForm} />
            </Switch>
          </section>
          <img src={bottomTransitionWave} className='bottomTransitionWave' alt='' />
        </Scrollbars>
      </Router>
    </Provider>
  );
};

export default App;
