import React, { Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getCurrentProfile } from '../../actions/profile';
import Spinner from '../layout/Spinner';
import { Link } from 'react-router-dom';
import { DashboardActions } from './DashboardActions';
import Scrollbars from 'react-custom-scrollbars';

const Dashboard = ({ getCurrentProfile, auth: { user }, profile: { profile, loading } }) => {
  useEffect(() => {
    getCurrentProfile();
  }, [getCurrentProfile]);

  return loading && profile === null ? (
    <Spinner />
  ) : (
    <Scrollbars className='scrollBarsRoot'>
      <section className=''>
        <Fragment>
          <h1 className='large text-primary my-marginup-only'>Dashboard</h1>
          <div className='lead'>
            <i className='fas fa-user'>
              <span className='ml-10'>
                {' '}
                Welcome
                {user && user.name}
              </span>
            </i>
          </div>
          <p className='my-3'>Your email is {user && user.email}</p>
          {profile !== null ? (
            <Fragment>
              <div className=''>as</div>
              <DashboardActions />
            </Fragment>
          ) : (
            <Fragment>
              <p>Please Create your Profile</p>
              <Link to='/create-profile' className='btn btn-gradient round-btn-only my-1'>
                Create Profile
              </Link>
            </Fragment>
          )}
        </Fragment>
      </section>
    </Scrollbars>
  );
};
//{user && user.name } means if user exists, show user.name

Dashboard.propTypes = {
  getCurrentProfile: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  profile: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  profile: state.profile,
});

export default connect(mapStateToProps, { getCurrentProfile })(Dashboard);
