import React, { Fragment, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Scrollbars from 'react-custom-scrollbars';
import { createProfile, getCurrentProfile } from '../../actions/profile';

function topFunction() {
  document.body.scrollTop = 0; // For Safari
  document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
}

const initialState = {
  company: '',
  companyInfo: '',
  streetAddress: '',
  cityDistrictStateAndCountry: '',
  contactNumber: '',
  email: '',
  gstinNumber: '',
};

const ProfileForm = ({ profile: { profile, loading }, createProfile, getCurrentProfile, history }) => {
  const [formData, setFormData] = useState(initialState);

  useEffect(() => {
    if (!profile) getCurrentProfile();
    if (!loading && profile) {
      const profileData = { ...initialState };
      for (const key in profile) {
        if (key in profileData) profileData[key] = profile[key];
      }

      setFormData(profileData);
    }
  }, [loading, getCurrentProfile, profile]);

  const {
    company,
    companyInfo,
    streetAddress,
    cityDistrictStateAndCountry,
    contactNumber,
    email,
    gstinNumber,
  } = formData;

  const onChange = (e) => setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();
    createProfile(formData, history, profile ? true : false);
  };

  return (
    <Scrollbars className='scrollBarsRoot'>
      <h1 className='large text-primary'>Edit Your Business Details</h1>
      <p className='lead'>
        <i className='fas fa-user' /> {'  '}Add some changes to your profile
      </p>
      <small>* = required field</small>

      <form className='form' onSubmit={(e) => onSubmit(e)}>
        <div className='form-group'>
          <input
            type='text'
            placeholder=' Company Name'
            name='company'
            value={company}
            className='round-btn-only'
            onChange={onChange}
            required
          />
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='Company Description'
            name='companyInfo'
            value={companyInfo}
            className='round-btn-only'
            onChange={onChange}
            required
          />
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='Street Address'
            name='streetAddress'
            value={streetAddress}
            className='round-btn-only'
            onChange={onChange}
            required
          />
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='City, District, State and Country Address'
            name='cityDistrictStateAndCountry'
            value={cityDistrictStateAndCountry}
            className='round-btn-only'
            onChange={onChange}
            required
          />
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='Contact Number'
            name='contactNumber'
            value={contactNumber}
            className='round-btn-only'
            onChange={onChange}
            required
          />
        </div>
        <div className='form-group'>
          <input
            type='email'
            placeholder='Email'
            name='email'
            value={email}
            className='round-btn-only'
            onChange={onChange}
            required
          />
        </div>
        <div className='form-group'>
          <input
            type='text'
            placeholder='GSTIN Number'
            name='gstinNumber'
            value={gstinNumber}
            className='round-btn-only'
            onChange={onChange}
            required
          />
        </div>

        <input
          type='submit'
          className='btn btn-primary my-1 btn-blue-gradient round-btn-only'
          onClick={() => topFunction()}
        />
        <Link className='btn btn-primary round-btn-only btn-blue-gradient ml-20' to='/dashboard'>
          Go Back
        </Link>
      </form>
    </Scrollbars>
  );
};

ProfileForm.propTypes = {
  createProfile: PropTypes.func.isRequired,
  getCurrentProfile: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  profile: state.profile,
});

export default connect(mapStateToProps, { createProfile, getCurrentProfile })(ProfileForm);
